import router from '../router'
import store from '@/store'

export default [
    {
        name:"goBack",
        methodAndroid:function () {
            var tmp = (store.state.routerModule.history-1) > 1;

            if(tmp){
                router.back(-1);
                store.commit("HISTORY",{num:-2});
            }
            return tmp;
        },
        methodIOS:function (data, responseCallback) {
            var tmp = (store.state.routerModule.history-1) > 1;

            if(tmp){
                router.back(-1);
                store.commit("HISTORY",{num:-2});
            }
            responseCallback(tmp);
        }
    },
    {
        name:"getPicture",
        methodAndroid:function (url) {
            alert(url);
            store.commit("OFFLINE_IMG",{picture:url});
            return true;
        },
        methodIOS:function (url, responseCallback) {
            store.commit("OFFLINE_IMG",{picture:url});
            responseCallback(true);
        }
    }
]




